@charset "UTF-8";
#navbar {
  width: 100vw;
  height: 7.5vh;
  background-color: rgb(220, 227, 241);
  position: fixed;
  top: 0;
  z-index: 999;
  display: block;
}
#navbar .content {
  width: calc(100% - 30rem); /* 여백이 없도록 설정 */
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: 0 15rem;
}
#navbar a {
  position: relative;
  color: #615d5d;
  font-weight: bold;
  font-size: 24px;
  margin: auto;
  text-decoration: none;
}
#navbar a::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 100%;
  height: 5px;
  border-radius: 3px;
  background-color: white;
  transition: right 0.3s ease; /* 오른쪽으로 이동하는 애니메이션 속성 추가 */
}
#navbar a:hover::before {
  right: 0;
  border: solid 1px rgb(209, 207, 207);
}
#navbar .cur {
  color: #3f68ee;
  position: relative;
  right: 0;
}
#navbar .cur::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5px;
  border-radius: 3px;
  background-color: white;
  transition: right 0.3s ease;
  border: solid 1px rgb(209, 207, 207);
}

/* 화면 크기가 768px 이하일 때 스타일 조정 */
@media (max-width: 768px) {
  .content {
    width: 100%;
    margin: 0; /* 작은 화면에서는 좀 더 좁게 조정 */
  }
}


